@import "parameters.scss";

/*--------------------------------------------------------------
# main-slider *
--------------------------------------------------------------*/
.wrapper {
  position: relative;
}
.main-slider__item {
  position: relative;
  height: 550px;
  .main-slider__control {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    z-index: 2;
    li {
      width: 15px;
      height: 15px;
      margin: 0 10px;
      box-sizing: border-box;
      border-radius: 50%;
      background-color: #fff;
      cursor: pointer;
      &.show {
        width: 15px;
        height: 15px;
        background-color: $basic-color-pink;
      }
    }
  }
}

.slide {
  position: absolute;
  width: 100%;
  color: #fff;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.6s, opacity 0.6s linear;
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #253746;
    opacity: 0.5;
    content: "";
  }
  img {
    width: 100%;
    height: 550px;
    object-fit: cover;
  }
  figcaption {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    .slide__title {
      max-width: 541px;
      font-family: 'Effra', 'Arial', sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      text-transform: uppercase;
      line-height: 28px;
    }
    .slide__text {
      margin-top: 18px;
      font-family: 'Effra', 'Arial', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 18px;
    }
  }
}

/*--------------------------------------------------------------
# calculator *
--------------------------------------------------------------*/
.calculator {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: rgba(37, 55, 70, 0.85);
  &-price {
    width: 100%;
    min-height: 60px;
    margin-bottom: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 30px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    color: $basic-color-pink;
    background: #F7F7FF;
  }
  &-form {
    width: 90%;
    display: flex;
    flex-direction: column;
    &__inner {
      width: calc(100% - 25px);
    }
    &__cities {
      position: relative;
    }
    &__cargo {
      position: relative;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &__label {
      position: relative;
      width: 140px;
      margin-top: 16px;
      display: flex;
      flex-direction: column;
      font-size: 12px;
      line-height: 20px;
      color: #F7F7FF;
      &.calculator-form__label--longest {
        width: 100%;
      }
      &.calculator-form__label--longer {
        width: 195px;
      }
      &-info {
        font-size: 12px;
        line-height: 20px;
        color: rgba(247, 247, 255, 0.5);
      }
      &-measure {
        position: absolute;
        top: 20px;
        right: 0;
        padding: 5px 10px;
        font-size: 15px;
        line-height: 20px;
        color: #253746;
      }
    }
    &__input {
      height: 30px;
      padding: 5px 10px;
      font-size: 15px;
      line-height: 20px;
      color: #253746;
      box-sizing: border-box;
      border: none;
      border-radius: 5px;
      background: #F7F7FF;
      outline: none;
      &.calculator-form__input--indent {
        padding: 5px 31px;
      }
/*      &::-webkit-calendar-picker-indicator {
        color: transparent;
        opacity: 1;
        background: none;
        background-size: contain;
      }*/
    }
    &__inputs-row {
      display: flex;
      border-radius: 5px;
      background: #F7F7FF;
      input {
        width: 30%;
        padding-left: 0;
        padding-right: 0;
        text-align: center;
      }
      span {
        padding: 5px 0;
        font-size: 15px;
        line-height: 20px;
        color: #253746;
      }
    }
    &-main__checkbox.checkbox {
      margin: 13px 0 0;
      font-size: 15px;
      line-height: 20px;
      color: #F7F7FF;
      input[type="checkbox"]:checked + span:before,
      input[type="checkbox"]:not(:checked) + span:before {
        border: 2px solid #fff;
        background-color: transparent;
      }

      input[type="checkbox"]:checked + span:after,
      input[type="checkbox"]:not(:checked) + span:after {
        border-left: 3px solid #fff;
        border-bottom: 3px solid #fff;
      }
    }
  }
}

.btn-arrow {
   position: absolute;
   top: 29px;
   display: block;
   width: 0;
   height: 0;
   margin: 0 5px;
   font-size: 0;
   border: 6px solid transparent;
   cursor: pointer;
   &.btn-arrow--right {
     left: 45px;
     border-left-color: #253746;;
     border-right: 0;
   }
   &.btn-arrow--left {
     left: 12px;
     border-right-color: #253746;;
     border-left: 0;
   }
 }

.date-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 30px;
  color: #F7F7FF;
  border-bottom: 4px solid #F7F7FF;
  .calculator-form__input {
    width: 150px;
  }
  .calculator-form__label {
    flex-direction: row;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-top: 25px;
  }
  &__text {
    margin-left: 3px;
    font-size: 15px;
    line-height: 20px;
    &.date-box__text--bold {
      font-weight: bold;
    }
  }
}

.btn-box {
  margin: 15px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.btn.btn--top {
  margin: 15px 0;
}

.btn-reverse {
  position: absolute;
  top: 50%;
  right: -40px;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}

.controls-cargo {
  position: absolute;
  top: 16px;
  right: -40px;
  display: flex;
  flex-direction: column;
  &__btn {
    width: 20px;
    height: 20px;
    padding: 0;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    color: #F7F7FF;
    box-sizing: border-box;
    border: 2px solid $basic-color-pink;
    border-radius: 5px;
    outline: none;
    background: $basic-color-pink;
    cursor: pointer;
    transition: 0.3s ease;
    &:hover {
      border: 2px solid #fff;
      background: transparent;
      transition: 0.3s ease;
    }
    &.controls-cargo__btn--white {
      border: 2px solid #F7F7FF;
      background: transparent;
      &:hover {
        border: 2px solid transparent;
        background: $basic-color-pink;
        transition: 0.3s ease;
      }
    }
  }
}